<script lang="ts">
  import { createEventDispatcher } from "svelte";
  import { copyScreenFeedback, debugging, state } from "../stores/app";

  import PickFile from "../../assets/icons/pickFile.svelte";
  import CopyScreen from "../../assets/icons/copyScreen.svelte";
  import SaveScreen from "../../assets/icons/saveScreen.svelte";
  import Debug from "../../assets/icons/debug.svelte";
  import Dump from "../../assets/icons/dump.svelte";
  import FullScreen from "../../assets/icons/fullScreen.svelte";
  import Run from "../../assets/icons/run.svelte";
  import Popover from "../components/popover.svelte";

  const pickFile = () => {
    fileInput.click();
  };

  $: loadHint = $state === "empty" 
  $: loaded = $state === "loaded"
  $: crashed = $state === "crashed"

  let fileInput: HTMLInputElement;

  const dispatch = createEventDispatcher();

  const handleFile = (e: Event) => {
    dispatch('romChanged', e);
  }

  const doCopyScreen = (e: Event) => {
    dispatch('doCopyScreen', e);
  }

  const doSaveScreen = (e: Event) => {
    dispatch('doSaveScreen', e);
}

  const doRun = (e: Event) => {
    dispatch('doRun', e);
  }

  const doFullscreen = (e: Event) => {
    dispatch('doFullscreen', e);
  }

  const doDump = (e: Event) => {
    dispatch('doDump', e);
  }

  const doDebug = (e: Event) => {
    dispatch('doDebug', e);
  }

  let ready = false;
  $: ready = $state === "ready";
</script>

<div class="tools-container">
  <div class="toolbar">
    <button
      class="toolbar-tool"
      type="button"
      tabindex="0"
      on:click={pickFile}
      id="btn_pickFile"
    >

    <Popover visible={loadHint}>
        Start by picking a BIN program
    </Popover>
      
      <PickFile />
      <input
        bind:this={fileInput}
        type="file"
        id="fileInput"
        style="display: none;"
        on:change={handleFile}
      />
    </button>

    <div
      class="separator"
      role="separator"
      data-orientation="horizontal"
      aria-orientation="horizontal"
    ></div>

    <button class="toolbar-tool" type="button" tabindex="0" on:click={doCopyScreen} id="btn_copyScreen">
      <Popover visible={$copyScreenFeedback !== null}>
        {$copyScreenFeedback}
      </Popover>

      <CopyScreen />
    </button>
    <button class="toolbar-tool" type="button" tabindex="0" on:click={doSaveScreen} id="btn_saveScreen">
      <SaveScreen />
    </button>
    <div
      class="separator"
      role="separator"
      data-orientation="horizontal"
      aria-orientation="horizontal"
    ></div>

    <button class="toolbar-tool" type="button" tabindex="0" id="btn_debug" on:click={doDebug} disabled={!loaded}>
      <Debug active={$debugging} />
    </button>
    <button class="toolbar-tool" type="button" tabindex="0" id="btn_dump" on:click={doDump} disabled={!loaded && !crashed}>
      <Popover visible={crashed}>
        Check why it crashed here
      </Popover>

      <Dump />
    </button>
    <button class="toolbar-tool" type="button" tabindex="0" id="btn_run" on:click={doRun} disabled={!loaded}>
        <Popover visible={loaded}>
            Now let's start the program
        </Popover>

        <Run />
        
    </button>

    <div
      class="separator"
      role="separator"
      data-orientation="horizontal"
      aria-orientation="horizontal"
    ></div>

    <button class="toolbar-tool" type="button" tabindex="0" on:click={doFullscreen} id="btn_fullscreen">
      <FullScreen />
    </button>
  </div>
</div>

<style>
  .tools-container {
    margin-left: 1.5em;
    visibility: visible;
    position: relative;
    display: flex;
    overflow: hidden;
    /* flex: 0 0 32px; */
    padding: 8px;
    font-family: ui-monospace, "Cascadia Code", "Source Code Pro", Menlo,
      Consolas, "DejaVu Sans Mono", monospace;
    font-weight: normal;
    color: var(--cp-text);
    min-height: 960px;
    margin-top: -40px;
    min-width: 80px;
  }

  .toolbar {
    background: #0d0d0d;
    border: 2px solid #2f2f2f;
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    gap: 8px;
    -webkit-user-select: none;
    user-select: none;
    padding: 0.6rem;
    border-radius: 0.6rem;
    color: #a1a1aa;
    height: 100%;
  }

  .toolbar-tool {
    border-radius: 0.25rem;
    background-color: transparent;
    display: inline-flex;
    block-size: 48px;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    min-inline-size: 48px;
    row-gap: 12px;
    position: relative;
    transition: background-color 0.25s ease-in-out;
    font-family: inherit;
    font-feature-settings: inherit;
    font-variation-settings: inherit;
    font-size: 100%;
    font-weight: inherit;
    line-height: inherit;
    color: inherit;
    margin: 0;
    padding: 0;
    border: none;
  }

  .toolbar-tool:disabled {
    cursor: not-allowed;
    opacity: 0.5;
    pointer-events: none;
  }

  .toolbar-tool:not(:disabled) {
    cursor: pointer;
    opacity: 1;
  }

  .toolbar-tool:not(:disabled):hover {
    background-color: #1c1c20;
  }

  button.toolbar-tool :global(svg) {
    width: 32px;
    height: 32px;
  }

  .separator {
    width: auto;
    margin: 0.5rem 4px;
    min-height: 2px;
    block-size: 2px;
    border-radius: 4px;
    inline-size: 100%;
    background-color: #202026;
  }
</style>
